@use "global" as *;

/* リキッドレイアウト対応 
PCは1280px,SPは365pxを基準。1280px~768pxまで,374px~は全体的に縮小していく */
/* SPファースト */
html {
  //~375px
  @media (max-width: 375px) {
    font-size: vw(375, 16);
  }

  //375px~787px
  font-size: 16px;
  @include mq("md") {
    font-size: vw(strip-unit($inner), 16);
  }

  //inner~max-screen
  @media (min-width: $inner) {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
a[href^="tel:"] {
  @include mq("md") {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: all 0.3s;
  @include mq("md") {
    &:hover {
      opacity: 0.8;
    }
  }
}